class Stats {
	constructor(options) {
		this.options = {
			...options
		}
	}

	quantile(arr, q) {
		const sorted = arr.sort((a, b) => a - b);
		const pos = (sorted.length - 1) * q;
		const base = Math.floor(pos);
		const rest = pos - base;

		if (sorted[base + 1] !== undefined) {
			return Math.floor(sorted[base] + rest * (sorted[base + 1] - sorted[base]));
		} else {
			return Math.floor(sorted[base]);
		}
	};

	prepareData(result) {
		return result.data.map(item => {
			item.date = item.timestamp.split('T')[0];

			return item;
		});
	}

	addMetricByDate(page, name, date) {
		let sampleData = this.data
				.filter(item => item.page === page && item.name === name && item.date === date)
				.map(item => item.value);

		return {
			page: page,
			hits: sampleData.length,
			p25: this.quantile(sampleData, 0.25),
			p50: this.quantile(sampleData, 0.5),
			p75: this.quantile(sampleData, 0.75),
			p95: this.quantile(sampleData, 0.95)
		};
	}

	addMetricByPeriod(page, name, start, end) {
		const sampleData = this.data
				.filter(item => item.page === page && item.name === name && item.date >= start && item.date <= end)
				.map(item => item.value);

		return {
			page: page,
			hits: sampleData.length,
			p25: this.quantile(sampleData, 0.25),
			p50: this.quantile(sampleData, 0.5),
			p75: this.quantile(sampleData, 0.75),
			p95: this.quantile(sampleData, 0.95)
		};
	}

	addMetricBySession(page, name, requestId) {
		const sampleData = this.data
				.filter(item => item.page === page && item.name === name && requestId === item.requestId)
				.map(item => item.value);

		return {
			page: page,
			requestId: requestId,
			hits: sampleData.length,
			p25: this.quantile(sampleData, 0.25),
			p50: this.quantile(sampleData, 0.5),
			p75: this.quantile(sampleData, 0.75),
			p95: this.quantile(sampleData, 0.95)
		};
	}

	showMetricsByDate(page, date) {
		const table = {
			connect: this.addMetricByDate(page, 'connect', date),
			fcp: this.addMetricByDate(page, 'FCP', date),
			lcp: this.addMetricByDate(page, 'LCP', date),
			fid: this.addMetricByDate(page, 'FID', date),
			historyLoaded: this.addMetricByDate(page, 'historyLoaded', date),
		};

		console.log(`   `);
		console.table(table);
	};

	showMetricByPeriod(page, start, end) {
		const table = {
			connect: this.addMetricByPeriod(page, 'connect', start, end),
			fcp: this.addMetricByPeriod(page, 'FCP', start, end),
			lcp: this.addMetricByPeriod(page, 'LCP', start, end),
			fid: this.addMetricByPeriod(page, 'FID', start, end),
			historyLoaded: this.addMetricByPeriod(page, 'historyLoaded', start, end),
		};

		console.log(`  c ${start}  ${end}`);
		console.table(table);
	}

	showMetricBySession(page, requestId) {
		const result = {
			connect: this.addMetricBySession(page, 'connect', requestId),
			fcp: this.addMetricBySession(page, 'FCP', requestId),
			lcp: this.addMetricBySession(page, 'LCP', requestId),
			fid: this.addMetricBySession(page, 'FID', requestId),
			historyLoaded: this.addMetricBySession(page, 'historyLoaded', requestId)
		};

		console.log(`    ${requestId}`);
		console.table(result);
	}

	compareMetric(page, name, section) {
		let sectionValues = new Set();
		let arr = [];

		for (let item of this.data) {
			sectionValues.add(item.additional[section])
		}

		for (let value of sectionValues) {
			let sampleData = this.data
				.filter(item => item.page === page && item.name === name && item.additional[section] === value)
				.map((item) => item.value);

			arr.push({
				name: name,
				page: page,
				hits: sampleData.length,
				p25: this.quantile(sampleData, 0.25),
				p50: this.quantile(sampleData, 0.5),
				p75: this.quantile(sampleData, 0.75),
				p95: this.quantile(sampleData, 0.95),
				[section]: value
			})
		}

		console.log(`    ${section}`);
		console.table(arr);
	}

	async fetchData() {
		return fetch(`https://shri.yandex/hw/stat/data?counterId=${this.options.counterId}`)
			.then(res => res.json())
			.then(result => {
				this.data = this.prepareData(result);
			});
	}
}

module.exports = Stats;